import React, { Component } from "react";

export default class ChannelForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      channel: ""
    };
  }
  onChange = e => {
    let { name, value } = e.target;
    this.setState({ [name]: value });
  };
  onSubmit = e => {
    e.preventDefault();
    console.log("Submiting ", this.state.channel);
    this.props.selectChannel(this.state.channel);
    this.setState({ channel: "" });
  };
  render() {
    return (
      <div className='row text-center'>
      <h2 className='text-center mt-3 col-12'>Agora Chat Application</h2>
      <form class="form-inline mt-5 text-center col-6 offset-3" onSubmit={this.onSubmit}>
        <div class="form-group mx-sm-3 mb-2">
          <label for="channel2" className="mr-3">Channel Name</label>
          <input type="text" class="form-control"
            name="channel"
            id="channel2"
            placeholder="Enter channel name"
            value={this.state.channel}
            onChange={this.onChange} />
        </div>
        <button type="submit" class="btn btn-primary mb-2">Join Channel</button>
      </form>
      </div>
    );
  }
}
