import React, { Component } from "react";
import AgoraRTC from "agora-rtc-sdk";
import { v4 as uuid } from 'uuid';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faVideo, faMicrophone, faMicrophoneSlash, faVideoSlash } from '@fortawesome/free-solid-svg-icons'
let client = AgoraRTC.createClient({ mode: "live", codec: "h264" });

const USER_ID = uuid()
const screenId = uuid()
const APP_ID = "1c721d1e1404469382d7d9b70d7f6bfa";

export default class Call extends Component {
  state = {
    enabledCamera: false,
    screenShare: false,
    enabledMic: false,
    isHost: false,
    isJoiner: false,
    remoteStreams: []
  };
  localStream = AgoraRTC.createStream({
    streamID: USER_ID,
    audio: true,
    video: true,
    screen: false,
    mediaSource: 'screen'
  });

  screenShareStream = AgoraRTC.createStream({
    streamID: USER_ID,
    audio: false,
    video: false,
    screen: true,
    mediaSource: 'screen'
  });



  componentDidMount() {
    this.initLocalStream();
    this.initClient();
    this.screenShareStream.on("stopScreenSharing", () => {
      this.screenShareStream.stop();
      this.screenShareStream.close();
      this.localStream.init();
      this.setState({ screenShare: !this.state.screenShare })
    });
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevProps.channel !== this.props.channel && this.props.channel !== "") {
      this.joinChannel();
    }
  }




  initLocalStream = () => {
    this.localStream.init(
      () => {
        console.log("getUserMedia successfully");
        this.localStream.play('agora_local');
      },
      (err) => {
        console.log("getUserMedia failed", err);
      }
    );
  };

  initClient = () => {
    client.init(
      APP_ID,
      () => {
        this.setState({ isHost: true })
        console.log("AgoraRTC client initialized");
      },
      (err) => {
        console.log("AgoraRTC client init failed", err);
      }
    );
    this.subscribeToClient();
  };

  subscribeToClient = () => {
    client.on("stream-added", this.onStreamAdded);
    client.on("stream-subscribed", this.onRemoteClientAdded);
    client.on("stream-removed", this.onStreamRemoved);
    client.on("peer-leave", this.onPeerLeave);
  };


  onStreamAdded = evt => {
    let stream = evt.stream;
    console.log("New stream added: " + stream.getId());
    this.setState(
      {
        remoteStreams: {
          ...this.state.remoteStream,
          [stream.getId()]: stream
        }
      },
      () => {
        client.subscribe(stream, (err) => {
          console.log("Subscribe stream failed", err);
        });
      }
    );
  };

  joinChannel = () => {
    let uid = uuid()
    client.join(
      '0061c721d1e1404469382d7d9b70d7f6bfaIACEVee8XVY3B6PsfC89H91sfEKU9fYHRz1o2RDPpLkPlKu95ggAAAAAEAAEa9nrkv5EYAEAAQCR/kRg',
      this.props.channel,
      uid,
      (uid) => {
        console.log("User " + uid + " join channel successfully");
        client.publish(this.localStream, (err) => {
          console.log("Publish local stream error: " + err);
        });

        client.on("stream-published", (evt) => {
          console.log("Publish local stream successfully");
        });
      },
      (err) => {
        console.log("Join channel failed", err);
      }
    );
  };

  onRemoteClientAdded = evt => {
    let remoteStream = evt.stream;
    this.state.remoteStreams[remoteStream.getId()].play(
      "agora_remote " + remoteStream.getId()
    );
  };

  onStreamRemoved = evt => {
    let stream = evt.stream;
    if (stream) {
      let streamId = stream.getId();
      let { remoteStreams } = this.state;

      stream.stop();
      delete remoteStreams[streamId];

      this.setState({ remoteStreams });

      console.log("Remote stream is removed " + stream.getId());
    }
  };

  onLeave = () => {
    client.leave(() => {
      console.log("client leaves channel");
    }, (err) => {
      console.log("client leave failed ", err);
      //error handling
    });
  }

  screenShare = () => {
    let screen = !this.state.screenShare;
    if (screen) {
      client.publish(this.screenShareStream)
      this.screenShareStream.init(
        () => {
          console.log("getUserMedia successfully");
          this.screenShareStream.play('agora_share');
        },
        (err) => {
          console.log("getUserMedia failed", err);
        }
      );
    }
    this.setState({ screenShare: screen })
  }

  cameraEnable = () => {
    if (this.state.enabledCamera) {
      this.localStream.enableVideo()
      this.setState({ enabledCamera: false })
    } else {
      this.localStream.disableVideo();
      this.setState({ enabledCamera: true })
    }
  }

  micEnableDisable = () => {
    if (this.state.enabledMic) {
      this.localStream.enableAudio();
      this.setState({ enabledMic: false })
    } else {
      this.localStream.disableAudio();
      this.setState({ enabledMic: true })
    }
  }

  onLeaveMeeting = () => {
    this.localStream.stop()
    client.leave();
  }

  onPeerLeave = evt => {
    let stream = evt.stream;
    if (stream) {
      let streamId = stream.getId();
      let { remoteStreams } = this.state;

      stream.stop();
      delete remoteStreams[streamId];
      this.setState({ remoteStreams });

      console.log(evt.uid + " leaved from this channel");
    }
  };

  render() {
    return (
      <div>
        <div className='row ml-5 mt-5'> <div className='col-md-6'>
          <div id="agora_local" className='img-thumbnail' style={{ height: '400px', width: '500px' }} />
        </div>
          <div className='col-md-6'>
            {Object.keys(this.state.remoteStreams).map(key => {
              let stream = this.state.remoteStreams[key];
              let streamId = stream.getId();
              return (
                <div key={streamId}>
                  <div
                    id={`agora_remote ${streamId}`}
                    className='img-thumbnail'
                    style={{ height: '400px', width: '500px' }}
                  >
                  </div>
                </div>
              );
            })}
          </div>
        </div>
        <div className='row mt-5 mb-5'>
          <button className='btn mr-3' onClick={() => this.cameraEnable()}><FontAwesomeIcon icon={this.state.enabledCamera ? faVideoSlash : faVideo} /></button>
          <button className='btn btn mr-3' onClick={() => this.micEnableDisable()}><FontAwesomeIcon icon={this.state.enabledMic ? faMicrophoneSlash : faMicrophone} /></button>
          <button className='btn btn-danger btn mr-3' onClick={(e) => this.onLeaveMeeting(e)}>Leave Meeting</button>
          {!this.state.screenShare ?<button className='btn btn-info btn mr-3' onClick={(e) => this.screenShare(e)}>Screen share</button>: ''}
        </div>
        {this.state.screenShare ? <div id="agora_share" style={{ height: '100vh' }} className='col-md-12'></div> : ''}
      </div>
    );
  }
}
